:root {
  --dark-color: #202020;
  --white-color: #fff;
  --gray-color: #a3a3a3;
  --orange-color: #ff6e30;
  --light-gray-color: #cacaca;
  --hover-color: #bc572b;
  --active-color: #ce4307;
}

@font-face {
  font-family: "Roboto";
  src: url(../styles/fonts/roboto-regular-webfont.woff2), url(../styles/fonts/roboto-regular-webfont.woff);
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url(../styles/fonts/roboto-bold-webfont.woff2), url(../styles/fonts/roboto-bold-webfont.woff);
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "TT First neue";
  src: url(../styles/fonts/ttfirsneue-bold-webfont.woff2), url(../styles/fonts/ttfirsneue-bold-webfont.woff);
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "TT Firs neue";
  src: url(../styles/fonts/ttfirsneue-regular-webfont.woff2), url(../styles/fonts/ttfirsneue-regular-webfont.woff);
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
*,
::before,
::after {
  box-sizing: border-box;
  outline: none;
}

html {
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
}

body {
  position: relative;
  overflow-x: hidden;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

section {
  margin-bottom: 40px;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 15px;
}

.section_title {
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  margin: 0;
}

.btn_reset {
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
}

.hashtag {
  display: inline-block;
  position: absolute;
  font-size: 14px;
  line-height: 16px;
  padding: 7px 14px;
  color: var(--white-color);
  background-color: var(--dark-color);
}

.header {
  margin-bottom: 38px;
}

.header__top {
  background-color: var(--dark-color);
  padding: 25px 0;
}

.header__top__inner {
  display: flex;
  align-items: center;
}
.header__top__inner .header__form {
  display: none;
}
.header__top__inner .tel__link {
  display: none;
}

.tablet_serch__btn {
  display: none;
}

.header__bottom__nav {
  display: none;
}

.logo {
  display: inline-block;
  width: 190px;
  height: 30px;
  margin-right: 138px;
}

.logo:focus {
  outline: 1px solid var(--orange-color);
}

.header__nav {
  margin-right: auto;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__list__item:not(:last-child) {
  margin-right: 69px;
}

.nav__link {
  font-family: "TT Firs Neue", sans-serif;
  padding: 0 5px;
  font-size: 16px;
  line-height: 21px;
  color: var(--white-color);
  transition: color 0.2s ease;
}

.nav__link:hover {
  color: var(--hover-color);
}

.nav__link:focus {
  background-color: var(--orange-color);
}

.nav__link:active {
  color: var(--active-color);
  background-color: transparent;
}

.social__link {
  display: inline-block;
}
.social__link svg path {
  fill: #cacaca;
}
.social__link:hover svg path {
  fill: var(--hover-color);
}
.social__link:focus svg path {
  fill: var(--orange-color);
}
.social__link:active svg path {
  fill: var(--active-color);
}

.social__link:not(:last-child) {
  margin-right: 20px;
}

.social__icon {
  width: 30px;
  height: 30px;
}

.header__bottom {
  padding: 11px 0;
  background-color: #4a4a4a;
}

.header__bottom__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.tel__link svg {
  fill: var(--gray-color);
  vertical-align: bottom;
}
.tel__link:hover svg path {
  fill: var(--hover-color);
}
.tel__link:hover .tel__number {
  color: var(--hover-color);
}
.tel__link:focus {
  color: var(--white-color);
  background-color: var(--orange-color);
}
.tel__link:focus svg path {
  fill: var(--gray-color);
}
.tel__link:active {
  background-color: transparent;
}
.tel__link:active svg path {
  fill: var(--active-color);
}
.tel__link:active .tel__number {
  color: var(--active-color);
}

.tel__number {
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  color: var(--white-color);
}

.header__form__btn {
  width: 16px;
  height: 16px;
  background-image: url("../img/svg/loupe.svg");
  background-repeat: no-repeat;
  background-position: center;
  object-fit: cover;
}

.header__form__search {
  width: 170px;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: var(--white-color);
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid #e9e9e9;
  color: var(--white-color);
}
.header__form__search:hover {
  border-bottom: 1px solid var(--hover-color);
}
.header__form__search:focus {
  border-bottom: 1px solid var(--hover-color);
}
.header__form__search:focus::placeholder {
  color: transparent;
}
.header__form__search:active {
  border-bottom: 1px solid var(--active-color);
}

.header__form__search::placeholder {
  font-family: "TT Firs Neue", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #E9E9E9;
}

.burger {
  display: none;
  flex-direction: column;
  width: 20px;
  height: 18px;
  cursor: pointer;
}

.burger__line {
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: var(--white-color);
}

.burger__line:last-child {
  width: 50%;
}

.burger__line:not(:last-child) {
  margin-bottom: 6px;
}

.nav__close_btn {
  display: none;
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 100px;
  background-color: #808080;
  margin-bottom: 16px;
  margin-left: 5px;
  cursor: pointer;
}

.hero__field {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 633px;
  gap: 0 30px;
}

.neon_photoset {
  position: relative;
  padding: 405px 145px 60px 60px;
  background-image: url("../img/images/hero-img_1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  object-fit: cover;
}
.neon_photoset .hashtag {
  top: 15px;
  right: 15px;
}

.hero__subtitle {
  font-size: 16px;
  line-height: 19px;
  color: var(--white-color);
}

.hero__title {
  max-width: 415px;
  font-family: "TT First neue", sans-serif;
  font-weight: 700;
  font-size: 36px;
  line-height: 104%;
  color: var(--white-color);
  margin-top: 0;
  margin-bottom: 24px;
}

.hero__link {
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  padding: 12px 19px;
  color: var(--white-color);
  background-color: var(--orange-color);
  border: 3px solid transparent;
}
.hero__link:hover {
  background-color: var(--hover-color);
  transition: all 0.2s ease;
}
.hero__link:focus {
  background-color: var(--orange-color);
  border: 3px solid var(--hover-color);
  transition: all 0.2s ease;
}
.hero__link:active {
  background-color: var(--active-color);
  border: 3px solid transparent;
  transition: all 0.2s ease;
}

.hero__photo__title {
  font-family: "TT First neue", sans-serif;
  margin-top: 0;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 24px;
  line-height: 65%;
  color: var(--dark-color);
}

.hero__grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(300px, 1fr));
  grid-template-rows: 345px 300px;
  gap: 30px;
}

.grid__item {
  grid-column: 1/3;
}

.studio {
  padding: 27px 0 70px;
  background-color: var(--dark-color);
}

.studio__title {
  margin: 0;
  margin-bottom: 5px;
  color: var(--white-color);
}

.studio__content_wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 30px;
}

.studio__descr {
  font-size: 16px;
  line-height: 28px;
  color: #cacaca;
  margin: 0;
}

.studio__img {
  margin-bottom: 20px;
}

.about__form .mail_label {
  width: 74.7%;
  margin-right: 30px;
}

.form_title {
  display: inline-block;
  font-family: "TT Firs Neue";
  font-size: 24px;
  line-height: 22px;
  color: var(--white-color);
  margin-bottom: 16px;
}

.about__form__input {
  width: 100%;
  padding: 15px 23px;
  font-size: 16px;
  line-height: 19px;
  color: var(--white-color);
  background-color: #4a4a4a;
  border: 1px solid transparent;
  outline: none;
}
.about__form__input:hover::placeholder {
  color: var(--hover-color);
}
.about__form__input:focus {
  border: 1px solid var(--orange-color);
}
.about__form__input:active {
  border: 1px solid transparent;
}

.about__form__input::placeholder {
  font-size: 16px;
  line-height: 19px;
  color: #CACACA;
}

.about__form__btn {
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  padding: 12px 19px;
  color: var(--white-color);
  background-color: var(--orange-color);
  border: 3px solid transparent;
  cursor: pointer;
}
.about__form__btn:hover {
  background-color: var(--hover-color);
  transition: all 0.2s ease;
}
.about__form__btn:focus {
  background-color: var(--orange-color);
  border: 3px solid var(--hover-color);
  transition: all 0.2s ease;
}
.about__form__btn:active {
  background-color: var(--active-color);
  border: 3px solid transparent;
  transition: all 0.2s ease;
}

.projects__title {
  margin: 0;
  margin-bottom: 12px;
}

.projects_window {
  width: 100%;
  max-width: 1290px;
  margin-bottom: 50px;
  display: grid;
  grid-template-columns: repeat(2, 100%);
  overflow-x: hidden;
}

.projects_content__wrapper {
  display: grid;
  width: 100%;
  max-width: 1290px;
  grid-template-columns: repeat(3, minmax(191px, 1fr)) repeat(2, minmax(300px, 1fr));
  grid-auto-rows: 300px;
  gap: 30px;
  overflow: hidden;
}

.card:nth-child(4),
.card:nth-child(5),
.card:nth-child(6) {
  grid-column: 1 span;
}

.card:nth-child(4) {
  grid-column: 1/2;
}

.projects_content_left {
  transform: translate(0);
  transition: transform 0.3s ease;
}

.projects_content_right {
  transform: translate(0);
  transition: transform 0.3s ease;
}

.projects_content_right.projects_content_right_active {
  transform: translate(-100%);
  transition: transform 0.3s ease;
}

.projects_content_left.projects_content_hidden {
  transform: translate(-100%);
  transition: transform 0.3s ease;
}

.card {
  position: relative;
}
.card .hashtag {
  top: 7px;
  right: 7px;
}
.card .card__date {
  position: absolute;
  left: 0;
  bottom: 0;
}

.card__img {
  margin-bottom: 18px;
}

.card__descr {
  font-size: 14px;
  line-height: 20px;
  color: var(--dark-color);
  margin-bottom: 35px;
}

.card__date img {
  width: 14px;
  height: 14px;
  vertical-align: middle;
}

.card__day {
  font-size: 12px;
  line-height: 16px;
  color: #999999;
}

.tablet_card {
  display: none;
}

.project__article {
  position: relative;
  padding-left: 30px;
  padding-top: 20px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  object-fit: cover;
}
.project__article .hashtag {
  top: 7px;
  right: 7px;
}
.project__article .card__date {
  margin-bottom: 6px;
}
.project__article .card__day {
  color: var(--white-color);
}

.project__article__title {
  max-width: 301px;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 23px;
  color: var(--white-color);
  margin: 0;
  margin-bottom: 6px;
}

.fuji {
  grid-column: 4/6;
  background-image: url("../img/images/projects-bg_1.jpg");
}

.oblivion {
  grid-column: 1/4;
  background-image: url("../img/images/projects-bg_2.jpg");
}

.portrait {
  grid-column: 1/4;
  background-image: url("../img/images/projects-bg_3.jpg");
}

.jas {
  grid-column: 4/6;
  background-image: url("../img/images/projects-bg_4.jpg");
}

.project__article__descr {
  max-width: 292px;
  font-size: 16px;
  line-height: 22px;
  color: var(--white-color);
}

.project__article__link {
  position: absolute;
  left: 30px;
  bottom: 34px;
  display: inline-block;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: var(--white-color);
  padding: 12px 19px;
  border: 3px solid var(--orange-color);
}
.project__article__link:hover {
  background-color: var(--orange-color);
  border: 3px solid var(--orange-color);
  transition: all 0.2s ease;
}
.project__article__link:focus {
  background-color: var(--hover-color);
  border: 3px solid var(--orange-color);
  transition: all 0.2s ease;
}
.project__article__link:active {
  background-color: var(--active-color);
  border: 3px solid var(--active-color);
  transition: all 0.2s ease;
}

.btns__wrapper {
  text-align: center;
}

.projects__btn {
  width: 30px;
  height: 30px;
  font-size: 16px;
  line-height: 1;
  color: #223444;
  background-color: var(--gray-color);
  text-align: center;
  transition: all 0.2s ease;
}
.projects__btn:hover {
  color: var(--orange-color);
  background-color: #e9e9e9;
}
.projects__btn:focus {
  color: #223444;
  background-color: #ffd4c1;
}
.projects__btn:active {
  color: var(--white-color);
  background-color: var(--orange-color);
}

.projects__btn:not(:last-child) {
  margin-right: 30px;
}

.projects__btn.projects__btn_active {
  color: var(--white-color);
  background-color: var(--orange-color);
}

.service {
  margin-bottom: 37px;
}

.service_container {
  display: grid;
  grid-template-columns: calc(67% - 15px) calc(33% - 15px);
  gap: 30px;
}

.service_title {
  margin-bottom: 9px;
}

.service__field {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(2, 100%);
  gap: 1px;
  overflow: hidden;
}

.service_types__field {
  position: relative;
  width: 412px;
  margin-bottom: 30px;
}

.service_type__inner {
  display: grid;
