}

.main_list__title {
  display: inline-block;
  font-family: "TT Firs Neue", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 22px;
  padding: 0 5px;
  margin: 0;
  margin-left: -5px;
  margin-bottom: 5px;
  color: var(--dark-color);
  transition: all 0.2s ease;
}
.main_list__title:hover {
  color: var(--orange-color);
}
.main_list__title:focus {
  color: var(--dark-color);
