}
.main_list__link span {
  font-weight: 700;
  font-size: 16px;
  line-height: 28px;
  color: var(--orange-color);
}
.main_list__link svg {
  width: 19px;
  height: 14px;
  vertical-align: middle;
  transition: width 0.2s ease;
}
.main_list__link svg path {
  fill: var(--orange-color);
}
.main_list__link:hover svg {
  width: 27px;
}
.main_list__link:focus {
  background-color: #ffd4c1;
}
.main_list__link:active span {
  color: var(--active-color);
}
.main_list__link:active svg path {
  fill: var(--active-color);
}

.service__right {
